.title {
  color: #4169e1;
}

.nav {
  display: flex;
}

.alert {
  color: #2a2a2a;
  -ms-user-select: none;
  user-select: none;
  cursor: pointer;
  visibility: hidden;
  opacity: 0;
  background-color: #fff;
  border: 1px solid #2a2a2a;
  border-radius: 3px;
  margin: 0;
  padding: 24px;
  font-size: 18px;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  bottom: 20px;
  right: 20px;
  transform: translateX(calc(100% + 20px));
}

.alert.success {
  color: #fff;
  background-color: #4caf50;
  border: 1px solid #388e3c;
}

.alert.is-visible {
  visibility: visible;
  opacity: 1;
  transform: translateX(0);
}

.timer {
  text-align: center;
  color: #212121;
  border-radius: 2px;
  margin-left: auto;
  margin-right: auto;
}

.clockface {
  color: #3f51b5;
  margin-top: 0;
  margin-bottom: 24px;
  font-family: Roboto Mono, monospace;
  font-size: 80px;
}

.timer-btn {
  min-width: 140px;
  text-transform: uppercase;
  color: #fff;
  cursor: pointer;
  background-color: #3f51b5;
  border: 0;
  border-radius: 3px;
  justify-content: center;
  margin-left: 4px;
  margin-right: 4px;
  padding: 10px 20px;
  font-size: 24px;
  display: inline-flex;
}

.timer-btn.is-active {
  background-color: #2196f3;
}

